.header__dropdown-menu {
	display: flex;
	align-items: center;
	// margin-left: 5%;
	// margin-top: auto;
	// margin-bottom: auto;
	flex-grow: 1;
	justify-content: space-between;
	// padding-left: 11px;


	.container {
		display: flex;
	}

	.dropdown-menu {
		position: relative;
		display: flex;
		align-items: center;
		margin-bottom: 4px;

		&__list {
			margin: 0;
			padding: 16px 0 0 0;
			list-style-type: none;
			display: none;
			flex-direction: column;
			background-color: #ffffff;
			margin-top: 0px;
			max-height: calc(100vh - 95px);
		}

		&:nth-child(1) {


			.dropdown-menu__title {
				display: flex;
				align-items: center;

				// &:before {
				// 	width: 14px;
				// 	height: 12px;
				// 	flex-shrink: 0;
				// 	content: '';
				// 	background: url('../images/neff-menuPic.svg');
				// 	background-repeat: no-repeat;
				// 	background-size: 14px 12px;
				// 	background-position: 0 center;
				// 	margin-right: 11px;
				// }
			}

			.dropdown-menu__link {
				padding: 9px 32px 9px 64px;
			}

		}

		&:nth-child(2) {}

		&:nth-child(3) {}

		&__title {

			font-family: "Roboto Condensed";
			font-size: 16px;
			font-weight: 400;
			line-height: 39px;
			text-transform: uppercase;
			padding: 0px 50px 0px 0px;
			color: #333333;
			box-shadow: 0 2px 21px 0 transparent;
			letter-spacing: 0.1px;
			cursor: pointer;
			background: url("../images/menu-oval.svg") 100%/9px no-repeat;
			white-space: nowrap;

			&.last {
				background: unset !important;

				a {
					color: #333333;
				}

				&:hover {
					a {
						color: $base;
						text-decoration: none;
					}
				}
			}

			&:hover {

				color: $base;
				// background: url("https://icongr.am/entypo/chevron-down.svg?color=bb0000") 97%/14px no-repeat;
			}

			&.last {
				padding: 9px 24px 9px 22px;
			}

		}

		&__link {
			color: #333333;
			font-family: "Roboto Condensed";
			font-size: 14px;
			font-weight: 400;
			line-height: 2;
			color: #333333;
			cursor: pointer;
			padding: 9px 32px;
			// background: #ffffff;

			&:hover {
				color: #b2b6b8;
				text-decoration: underline;
			}
		}

		&.active {


			.dropdown-menu__list {
				display: flex;
				overflow-y: auto;
				// margin-right: -16px;
				// padding-right: 20px;

			}

			.dropdown-menu__item {
				display: flex;
				flex-direction: column;
				background-repeat: no-repeat !important;
				background-position: 21px 9px !important;
			}

			.dropdown-menu__link {
				white-space: nowrap;
			}

			.dropdown-menu__title {
				// box-shadow: 0 -10px 21px 0 rgba(0, 0, 0, 0.1);
				z-index: 999;
				position: relative;

				// color: $base;
				// background: url("https://icongr.am/entypo/chevron-up.svg?color=bb0000") 97%/14px no-repeat;
				&:before {
					content: '';
					width: calc(100% - 50px);
					height: 2px;
					position: absolute;
					bottom: 0;
					background: $base;
				}
			}

			.dropdown-menu__parent {
				position: absolute;
				overflow: hidden;
				box-shadow: 0px 2px 21px rgba(0, 0, 0, 0.09);
				z-index: 99999;
				top: 39px;
			}

		}

		&.collection {
			.dropdown-menu__title {
				background: unset !important;
				color: #ba1212;
				font-family: "Roboto Condensed";
				font-size: 16px;
				font-weight: 700;
				line-height: 39px;
				padding: 0px 5px 0px 0px;
			}


			.sub-menu__list {
				display: none;
			}

			.sub-menu__link {
				z-index: 9;

				&:hover {
					text-decoration: none;

					&:after {
						content: '';
						width: calc(100% - 12px);
						height: 1px;
						position: absolute;
						bottom: 0;
						right: 0;
						border-bottom: 1px dashed;
					}
				}
			}

			.dropdown-menu__parent {
				flex-direction: row;
				left: -53px;
				right: -53px;
				max-width: calc(100% + 104px);
				background-color: #fff;
				padding-right: 0;
				max-height: calc(100vh - 150px);
				overflow: visible;
				position: absolute;
				-webkit-box-shadow: 0 18px 41px 0 rgba(0, 0, 0, .1);
				box-shadow: 0 18px 41px 0 rgba(0, 0, 0, .1);
				z-index: 99999;
				top: 100%;
				width: auto;
				min-width: 198px;

				&:before {}

				.dropdown-menu__list {
					height: auto;
					display: flex;
					flex-direction: row;
					flex-wrap: wrap;
					padding: 12px 40px;
					justify-content: space-between;
				}
			}

			.dropdown-menu__reference {
				color: inherit;
			}

			.dropdown-menu__image {
				img {
					border: 1px solid transparent;
				}

				&:hover {
					img {
						border: 1px solid $base;

					}

					+.dropdown-menu__subtitle .sub-menu__list {
						display: flex;

						&:after {
							content: '';
							border-bottom: 1px dashed transparent;
							position: absolute;
							// bottom: 8px;
							width: calc(100% + 2px);
							height: calc(100% + 44px);
							left: 0;
							top: -43px;
							box-shadow: 0px 2px 21px rgba(0, 0, 0, 0.09);
						}

					}
				}
			}

			.dropdown-menu__item {
				margin: 17px 17px 7px;
				width: calc((100%/4) - 35px);
				position: relative;

				.dropdown-menu__subtitle {
					padding: 13px 16px 6px;
					position: relative;
					width: fit-content;
					cursor: pointer;

					&:before {
						content: '';
						border-bottom: 1px dashed transparent;
						position: absolute;
						bottom: 8px;
						width: calc(100% - 32px);
					}

					&:hover {
						color: $base;

						// box-shadow: 0px 2px 21px rgba(0, 0, 0, 0.09);
						.dropdown-menu__reference {
							position: relative;
							display: flex;
							z-index: 4;
							text-decoration: none;
						}

						&:before {
							border-bottom: 1px dashed $base;
						}

						& .sub-menu__list {
							display: flex;

							&:after {
								content: '';
								border-bottom: 1px dashed transparent;
								position: absolute;
								// bottom: 8px;
								width: calc(100% + 2px);
								height: calc(100% + 44px);
								left: 0;
								top: -43px;
								box-shadow: 0px 2px 21px rgba(0, 0, 0, 0.09);
							}

						}

					}
				}

				.sub-menu__link {
					word-spacing: 2px;
					position: relative;
					padding-left: 11px;
					line-height: 23px;
					margin-bottom: 3px;

					&:before {
						content: '? ';
						position: absolute;
						left: 0;
					}
				}

				.sub-menu__list {
					padding-left: 16px;
					background: #ffffff;
					box-shadow: inherit;
					position: absolute;
					width: 267px;
					z-index: 2;
					top: 43px;
					left: -2px;
				}
			}

			.show-more {
				color: $base;
				font-family: Arial;
				font-size: 13px;
				font-weight: 400;
				line-height: 18px;
				margin: 16px 0;
				z-index: 4;

			}

			&.active {
				.dropdown-menu__parent {
					display: flex;

				}

				.dropdown-menu__title {
					box-shadow: 0 -10px 21px 0 rgba(0, 0, 0, 0.1);
					z-index: 999;
					position: relative;
					color: $base;
					background: url("https://icongr.am/entypo/chevron-up.svg?color=394791") 97% 12px/14px no-repeat;
				}

				// .dropdown-menu__list {
				// 	height: auto;
				// }
			}
		}
	}

	.header__banner {
		width: 30%;
	}

	.sub-menu__list {
		display: flex;
		flex-direction: column;
		padding-left: 30px;
		list-style: none;
	}


	.sub-menu__item {
		display: flex;
		padding-right: 16px;
	}

	.sub-menu__link,
	.sub-menu__item {
		color: #333333;
		font-family: "Roboto Condensed";
		font-size: 14px;
		font-weight: 400;
		line-height: 29px;
		white-space: nowrap;

		&:hover {
			color: $base;
		}
	}

	@media (max-width: 1200px) {
		margin-left: 58px;
	}

	.time-work {
		display: none;
	}
}

@media (max-width: 767px) {
	.menu.active {
		// height: 100vh;
		height: auto;
		// overflow-y: scroll;
	}

	.header__dropdown-menu {
		align-items: baseline;
		width: unset;
		display: flex;
		flex-direction: column;
		position: unset;
		order: -1;
		margin-left: 0;
		padding-left: 0;
		width: 100%;
		flex-grow: 0;


		.container {
			padding: 0;
			padding-top: 3px;
			margin: 0;
		}

		.dropdown-menu {
			display: flex;
			margin-left: 0 !important;
			flex-wrap: wrap;
			padding-left: 0px;
			width: 100%;

			&__three {
				margin-bottom: 0;
				border-bottom: 0;
			}

			// &:first-child {
			// 	background: url("../images/dp-2.svg") no-repeat 0 10px;

			// }

			// &:nth-child(2) {
			// 	background: url("../images/dp-1.svg") no-repeat 0 10px;

			// }

			&:nth-child(3) {
				display: none;
			}
		}

		.dropdown-menu__back {
			display: none;
		}

		.header__telephones {
			// display: none !important;
		}

		.time-work {
			color: #666;
			font-family: Arial, sans-serif;
			font-size: 13px;
			line-height: 22px;
			text-align: left;
			display: block;

			i {
				color: #666;
				font-family: Arial, sans-serif;
				font-size: 13px;
				line-height: 22px;
				text-align: left;
				font-style: normal;
				margin-right: 4px;
			}
		}

		.header__telephones {
			display: flex;
			flex-wrap: wrap;
			margin-top: 0;
			align-items: baseline;
			order: 3;
			max-height: 190px;
			padding-top: 1rem;
		}
	}

	.menu-480 {
		.header__dropdown-menu {
			display: none !important;
		}

		background: url("https://icongr.am/entypo/chevron-down.svg?color=eeeeee")100% 50%/18px no-repeat !important;

		&:hover {
			background: url("https://icongr.am/entypo/chevron-down.svg?color=394791") 100% 50%/18px no-repeat !important;
			background-color: #5a5a5a;
		}
	}

	.header__dropdown-menu {
		.dropdown-menu {
			margin-bottom: 0;

			&__title {
				font-family: "Roboto Condensed";
				font-size: 16px;
				font-weight: 400;
				line-height: 46px;
				padding: 2px 0 0 0;
				width: 100%;

				// &:hover {
				// 	box-shadow: none !important;
				// }
				&:before {
					display: none;
				}


				&.last {
					padding: 13px 0 0 0;
				}
			}

			.dropdown-menu__parent {
				margin-left: 0px;
				width: 100%;
			}

			&.active {

				.dropdown-menu__list,
				.dropdown-menu__parent {
					position: unset;
					box-shadow: none;
					padding: 0;
					margin-top: 0;
					margin-right: 0px;
				}

				.dropdown-menu__list {
					display: flex;
					flex-wrap: wrap;
					flex-direction: row;
					justify-content: space-between;
				}


				.dropdown-menu__title {
					box-shadow: none !important;
				}

				.dropdown-menu__item {
					width: 30%;
					height: 95px;
					display: flex;
					flex-direction: column;
					padding-top: 70px;
					background-size: auto 47px !important;
					margin: 9px 0 27px;
					background-position: 50% 9px !important;
					align-items: center;
				}
			}
		}

		.sub-menu__list {
			padding-left: 13px;
			margin-bottom: 20px;
		}

		.sub-menu__link {
			color: #666666;
			font-size: 13px;
			font-weight: 400;
			line-height: 28px;
		}

		.dropdown-menu__link {
			padding: 3px 0;
			color: #333333;
			font-family: "Roboto Condensed";
			font-size: 12px;
			font-weight: 400;
			line-height: 14px;
			text-align: center;
			white-space: normal !important;
			width: 82px;
		}
	}

	.menu__item {
		display: flex;
		position: relative;
		flex-wrap: wrap;

		&:first-child {


			// &:hover {
			// 	background: url("https://icongr.am/entypo/chevron-down.svg?color=394791") 100% 50%/18px no-repeat;
			// 	background-color: #262626;
			// }

			span {
				width: 100%;
			}
		}
	}

}

